html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  h2 {
    font-size: 1.5rem;
  }
}
@media (min-width: 481px) {
  h2 {
    font-size: 1.75rem;
  }
}
@media (min-width: 1025px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-family: "Gotham-Bold";
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 481px) {
  h3 {
    font-size: 1.9rem;
  }
}
@media (min-width: 1025px) {
  h3 {
    font-size: 2.2rem;
  }
}
@media (min-width: 1201px) {
  h3 {
    font-size: 2.5rem;
  }
}

p {
  font-family: "CamingoDosProCd-SemiBold";
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  p {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  p {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  p {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  p {
    font-size: 1.9rem;
  }
}

.info-text a {
  font-family: "CamingoDosProCd-SemiBold";
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  .info-text a {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  .info-text a {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  .info-text a {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  .info-text a {
    font-size: 1.9rem;
  }
}

ul {
  padding-left: 0;
}
ul p {
  padding-left: 2rem;
  padding-right: 2rem;
}
@media (min-width: 768px) {
  ul {
    font-size: 2rem;
  }
}

li {
  list-style: disc;
  list-style-position: inside;
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  li {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  li {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  li {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  li {
    font-size: 1.9rem;
  }
}

form p {
  font-size: 1.1rem;
}
@media (min-width: 320px) {
  form p {
    font-size: 1.35rem;
  }
}
@media (min-width: 481px) {
  form p {
    font-size: 1.45rem;
  }
}
@media (min-width: 1025px) {
  form p {
    font-size: 1.65rem;
  }
}
@media (min-width: 1201px) {
  form p {
    font-size: 1.85rem;
  }
}

a {
  font-size: 1.3rem;
  text-decoration: none;
  color: unset;
}
@media (min-width: 481px) {
  a {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  a {
    font-size: 1.6rem;
  }
}
@media (min-width: 1201px) {
  a {
    font-size: 1.8rem;
  }
}

td {
  font-size: 1.1rem;
}
@media (min-width: 481px) {
  td {
    font-size: 1.25rem;
  }
}
@media (min-width: 1025px) {
  td {
    font-size: 1.5rem;
  }
}

tr {
  vertical-align: middle;
}

button,
.btn {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  button,
  .btn {
    font-size: 1.5rem;
  }
}
.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
}
@media (min-width: 320px) {
  .form-select,
  .form-control,
  .invalid-feedback {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) {
  .form-select,
  .form-control,
  .invalid-feedback {
    font-size: 1.75rem;
  }
}
label {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  label {
    font-size: 1.5rem;
  }
}
@media (min-width: 768px) {
  label {
    font-size: 2rem;
  }
}
h1 {
  color: red;
}